import { assignValue } from "@app/_core/helpers/utils";
import { FG_ITEM } from "@app/_shared/shared.type.js";

import * as THREE from 'three';
type fn = (...args: any[]) => any;
type Float = number;
type Integer = number;

declare class Raycaster {
    constructor(origin: Vector3, direction: Vector3, near: Float, far: Float)

}

declare class Matrix4 {

}

declare class Euler {
    constructor(x: Float, y: Float, z: Float, order: String)
    order: String
    x: Float
    y: Float
    z: Float
    copy(euler: Euler): Euler
    clone(): Euler
    equals(euler: Euler): Boolean
    fromArray(array: any[]): Euler
    reorder(newOrder: String): Euler
    set(x: Float, y: Float, z: Float, order: String): Euler
    setFromRotationMatrix(m: Matrix4, order: String): Euler
    setFromQuaternion(q: Quaternion, order: String): Euler
    setFromVector3(vector: Vector3, order: String): Euler
    toArray(array: any[], offset: Integer): any[]
    toVector3(optionalResult: Vector3): Vector3
}

export interface Vector3 {
    x: number
    y: number
    z: number
}
export interface SizeIn3D {
    width: number
    height: number
    depth: number
}
export interface Vector2 {
    x: number
    y: number
}

export interface Color {
    r: number
    g: number
    b: number
    set(...args: any[]): this

    setScalar(scalar: number): this

    setHex(hex: string): this
    setRGB(r: number, g: number, b: number): this

    setHSL(h, s, l): this

    setStyle(style): this

    setColorName(style): this
    clone(): Color
    copy(color): this

    getHex(): number

    getHexString(): string

    getHSL(target): any

    getStyle(): string
    [x: string]: any

}

export interface Texture {
    image?: any,
    flipY?: boolean
    [x: string]: any
}

export interface MeshMaterial {
    type?: string
    color?: string | number | Color
    map?: Texture
    bumpMap?: Texture
    bumpScale?: number
    normalMap?: Texture
    normalMapType?: number
    normalScale?: any
    side?: number
    opacity?: number
    transparent?: boolean
    visible?: boolean
    [x: string]: any
}

export interface Quaternion {
    x: number
    y: number
    z: number
    w: number
}

export interface EventDispatcher {
    addEventListener(type: String, listener: fn): null
    hasEventListener(type: String, listener: fn): Boolean
    removeEventListener(type: String, listener: fn): null
    dispatchEvent(event: Object): null
}

export interface Object3D extends EventDispatcher {
    animations: any
    castShadow: boolean
    children: Object3D
    customDepthMaterial: MeshMaterial
    customDistanceMaterial: MeshMaterial
    frustumCulled: boolean
    id: number
    layers: any
    matrix: any
    matrixAutoUpdate: Boolean
    matrixWorld: any
    matrixWorldNeedsUpdate: Boolean

    modelViewMatrix: any
    name: String
    normalMatrix: any
    onAfterRender: Function
    onBeforeRender: Function
    parent: Object3D
    position: Vector3
    quaternion: any
    receiveShadow: boolean
    renderOrder: Number
    rotation: any
    scale: Vector3
    up: Vector3
    userData: Object
    uuid: String
    visible: Boolean


    add(object: Object3D, ...objects: Object3D[]): this
    applyMatrix4(matrix: any): null
    applyQuaternion(quaternion: Quaternion): this

    attach(object: Object3D): this

    clone(recursive: Boolean): Object3D

    copy(object: Object3D, recursive: Boolean): this
    getObjectById(id: number): Object3D
    getObjectByName(name: String): Object3D
    getObjectByProperty(name: String, value: any): Object3D
    getWorldPosition(target: Vector3): Vector3
    getWorldQuaternion(target: Quaternion): Quaternion
    getWorldScale(target: Vector3): Vector3
    getWorldDirection(target: Vector3): Vector3
    localToWorld(vector: Vector3): Vector3
    lookAt(vector: Vector3): null
    lookAt(x: number, y: number, z: number): null
    raycast(raycaster: Raycaster, intersects: Object3D[]): Object3D[]

    Abstract(): any

    remove(object: Object3D, ...objects: Object3D[]): this
    removeFromParent(): this
    clear(): this

    rotateOnAxis(axis: Vector3, angle: number): this

    rotateOnWorldAxis(axis: Vector3, angle: Float): this

    rotateX(rad: Float): this

    rotateY(rad: Float): this

    rotateZ(rad: Float): this

    setRotationFromAxisAngle(axis: Vector3, angle: Float): null

    setRotationFromEuler(euler: Euler): null

    setRotationFromMatrix(m: Matrix4): null

    setRotationFromQuaternion(q: Quaternion): null

    toJSON(meta: Object): Object

    translateOnAxis(axis: Vector3, distance: Float): this

    translateX(distance: Float): this

    translateY(distance: Float): this

    translateZ(distance: Float): this

    traverse(callback: Function): null

    traverseVisible(callback: Function): null

    traverseAncestors(callback: Function): null

    updateMatrix(): null

    updateMatrixWorld(force: Boolean): null

    updateWorldMatrix(updateParents: Boolean, updateChildren: Boolean): null

    worldToLocal(vector: Vector3): Vector3
}

export interface ThreeMesh {
    castShadow: boolean,
    children: Object3D,
    customDepthMaterial: MeshMaterial,// 'Material',
    customDistanceMaterial: MeshMaterial,// 'Material',
    frustumCulled: boolean,
    id: number,// 'Integer',
    layers: any,// 'Layers',
    matrix: any,
    material: MeshMaterial,// 'Material',
    matrixAutoUpdate: boolean,
    matrixWorld: any,// 'Matrix4',
    matrixWorldNeedsUpdate: boolean,
    modelViewMatrix: any,// 'Matrix4',
    name: string,// 'String',
    normalMatrix: any,// 'Matrix3',
    onAfterRender: () => any,// 'Function',
    onBeforeRender: () => any,// 'Function',
    parent: Object3D,// 'Object3D',
    position: Vector3,
    quaternion: any,// 'Quaternion',
    receiveShadow: boolean,
    renderOrder: number,
    rotation: Vector3,
    scale: Vector3,
    up: Vector3,
    userData: any,
    uuid: string,// 'String',
    visible: boolean
}
